import React, { useEffect } from "react";
import { Route, BrowserRouter, Redirect } from "react-router-dom";
import { IonApp, IonRouterOutlet, IonSplitPane } from "@ionic/react";
import { IonReactRouter } from "@ionic/react-router";
import Menu from "./components/Menu";
import { initialize, log } from "./util/logger";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "./theme/variables.css";
import Login from "./pages/Login";
import AuthGate from "./components/AuthGate";

import { ApplicationUserContextProvider } from "./context/ApplicationUserContext";
import Logout from "./pages/Logout";
import { AuthorizedItemsContextProvider } from "./context/AuthorizedItemsContext";
import env from "./api/environment";
import { ScreenOrientation } from "@ionic-native/screen-orientation";
import { MessagesContextProvider } from "./context/MessagesContext";

const MyApp: React.FC = () => {
  useEffect(() => {
    if (env.settings.hasCapacitor) {
      ScreenOrientation.lock(ScreenOrientation.ORIENTATIONS.PORTRAIT).then(
        () => {
          console.log("screen locked to portrait");
        }
      );
    }

    initialize();
    log("app_render", {
      vers: env.settings.appVersionString,
      platform: env.settings.platform,
    });
  }, []);

  return (
    <IonApp>
      <IonReactRouter>
        <IonReactRouter>
          <IonSplitPane contentId="main">
            <Menu />
            <IonRouterOutlet id="main">
              <Route
                path="/login"
                render={(routeProps) => <Login {...routeProps} />}
              />

              <Route
                path="/logout"
                render={(routeProps) => <Logout {...routeProps} />}
              />

              <Route
                path="/tabs"
                render={(routeProps) => <AuthGate {...routeProps} />}
              />

              <Route path="/" exact={true}>
                <Redirect to="/tabs/schedule" />
              </Route>
            </IonRouterOutlet>
          </IonSplitPane>
        </IonReactRouter>
      </IonReactRouter>
    </IonApp>
  );
};

const App: React.FC = () => {
  return (
    <BrowserRouter>
      <ApplicationUserContextProvider>
        <AuthorizedItemsContextProvider>
          <MessagesContextProvider>
            <MyApp />
          </MessagesContextProvider>
        </AuthorizedItemsContextProvider>
      </ApplicationUserContextProvider>
    </BrowserRouter>
  );
};

export default App;
