import React from "react";
// import "./TodayEventCard.scss";
import "../global-styles.scss";
import Skeleton from "react-loading-skeleton";

interface TodayEventCardProps {}

// --------------------------------------------------------------------------------------
const TodayEventCardSkeleton: React.FC<TodayEventCardProps> = () => {
  return (
    <button className="card">
      <div style={{ paddingLeft: "20px", paddingRight: "2px" }}>
        <Skeleton width={5} height={40} />
      </div>
      <div className="card-second-col">
        <Skeleton width={150} />
        <div className="dept-position-row">
          <Skeleton width={150} />
        </div>
      </div>
    </button>
  );
};

export default TodayEventCardSkeleton;
