import React, { CSSProperties, useContext } from "react";
import { IonIcon } from "@ionic/react";
// import "./TodayEventCard.scss";
import "../global-styles.scss";
import {
  getBreakStartEnd,
  getDurationStringFromTimeSpan,
  getEventStartEnd,
  getStartTime,
} from "../util/time-helper";
import { AuthorizedLocation } from "../orbital-interfaces/authorized-items";
import EventType from "../enums/EventType";
import EventStatus from "../enums/EventStatus";
import { cafe, time } from "ionicons/icons";
import { EventInformation } from "../orbital-interfaces/TEC";
import {
  getEventEditAuthorization,
  EventEditAuthorization,
} from "./EditEvent/EditEventAuthorization";
import { AuthorizedItemsContext } from "../context/AuthorizedItemsContext";

interface TodayEventCardProps {
  event?: EventInformation;
  locations: AuthorizedLocation[];
  onEdit: (ev: EventInformation, eventEditAuth: EventEditAuthorization) => void;
}

const colorMap: any = {
  1: "#9CBA6A",
  2: "#BFBFBF",
  3: "#FFAD00",
  4: "#C41A26",
  6: "#677A46",
  7: "#FFAD00",
};

const statusTimeColor: any = {
  1: { color: "black" },
  2: { color: "#C41A26" },
  4: { color: "#BCBCBC" },
};

// --------------------------------------------------------------------------------------
function getPosition(locations: AuthorizedLocation[], ev: any) {
  const thisLocation = locations.filter((loc) => loc.id === ev.LocationId)[0];
  const multiLocation =
    locations.filter((l) =>
      l.departments.filter((d) => d.positions.filter((p) => p.hasPosition))
    ).length > 1;
  const multiDepartment = multiLocation
    ? true
    : thisLocation.departments.filter((d) =>
        d.positions.filter((p) => p.hasPosition)
      ).length > 1;

  let posDisplay = "";
  if (multiLocation) {
    if (ev.AbbreviatedLocationName) {
      posDisplay = ev.AbbreviatedLocationName + "/";
    } else if (ev.LocationAbbreviation) {
      posDisplay = ev.LocationAbbreviation + "/";
    }
  }

  if (multiDepartment) {
    if (ev.AbbreviatedDepartmentName) {
      posDisplay += ev.AbbreviatedDepartmentName + "/";
    } else if (ev.DepartmentAbbreviation) {
      posDisplay += ev.DepartmentAbbreviation + "/";
    }
  }

  posDisplay += ev.PositionName;

  return posDisplay;
}

// --------------------------------------------------------------------------------------
const TodayEventCard: React.FC<TodayEventCardProps> = ({
  event,
  locations,
  onEdit,
}: TodayEventCardProps) => {
  const { auth } = useContext(AuthorizedItemsContext);

  if (!event || !auth || !auth.locations || auth.locations.length === 0) {
    return <div />;
  }

  const noEndTime = new Date(9000, 1, 1);
  const onClock =
    event.EventTypeId === EventType.Clock &&
    event.EventStatusId === EventStatus.Approved &&
    event.IsClockedIn;
  const onBreak =
    event.EventTypeId === EventType.Clock &&
    event.EventStatusId === EventStatus.Approved &&
    event.EventBreaks.filter((b) => !b.BreakEnd || b.BreakEnd > noEndTime)
      .length > 0;
  const timeColor: CSSProperties = statusTimeColor[event.EventStatusId];

  const displayIcon = onClock && onBreak ? cafe : time;
  const rangeDisplay = onClock ? (
    <div className="time-row">
      <IonIcon icon={displayIcon} />
      <span style={timeColor}>{getStartTime(event)}</span>{" "}
      {getDurationStringFromTimeSpan(
        event.EventTypeId === EventType.Absent
          ? event.Time
          : event.EventDuration
      )}{" "}
    </div>
  ) : (
    <div className="time-row">
      <span style={timeColor}>{getEventStartEnd(event)}</span>{" "}
      {getDurationStringFromTimeSpan(event.EventDuration)}{" "}
    </div>
  );

  // ------------------------------------------------------------------------------------
  const onButtonClick = () => {
    const eventEditAuth = getEventEditAuthorization(
      event.EventTypeId,
      event.EventStatusId,
      event.EmployeeId,
      event.EventStart,
      event.EventEnd,
      event.LocationId,
      event.DepartmentId!,
      event.PositionId,
      event.IsConfirmed,
      auth
    );
    if (eventEditAuth === null) {
      return;
    }

    onEdit(event, eventEditAuth);
  };

  const tag = event.Tag ? " ~" + event.Tag : "";

  const eventTypeBorder = {
    borderLeft: `7px solid ${colorMap[event.EventTypeId]} `,
  };
  return (
    <button onClick={onButtonClick} className="card">
      <div className="card-second-col" style={eventTypeBorder}>
        {rangeDisplay}
        <div className="dept-position-row">
          {getPosition(locations, event)} {tag}
        </div>
        {event.EventBreaks &&
          event.EventBreaks.map((brk) => (
            <div key={brk.BreakStart} className="break-div">
              (B) {getBreakStartEnd(brk)}
            </div>
          ))}
      </div>
    </button>
  );
};

export default TodayEventCard;
