import React from "react";
import { IonContent, IonPage, useIonViewDidEnter } from "@ionic/react";
import { RouteComponentProps } from "react-router";
import { ApplicationUserContext } from "../context/ApplicationUserContext";
// import "./Logout.scss";
import "../global-styles.scss";
import { log } from "../util/logger";
import { AuthorizedItemsContext } from "../context/AuthorizedItemsContext";

interface LogoutProps extends RouteComponentProps {}

const Logout: React.FC<LogoutProps> = ({ history }: RouteComponentProps) => {
  const { user, logout } = React.useContext(ApplicationUserContext);
  const { clearAuth } = React.useContext(AuthorizedItemsContext);

  useIonViewDidEnter(() => {
    Promise.all([clearAuth(), logout()]).then(() => {
      log("logout", { user: user.username });
      console.log(`logged out (${user.username})`);
      setTimeout(() => {
        history.push("/login", { direction: "none" });
        history.goForward();
      }, 500);
    });
  });

  return (
    <IonPage id="logout-page">
      <IonContent className="logout-content">
        <div className="login-logo">
          <img
            className="logo"
            src="assets/img/OS Logo - 4pt Rounded _ 2 Tone Orange.svg"
            alt="Orbital Shift Logo"
          />
        </div>

        <div className="label">You were logged out</div>
      </IonContent>
    </IonPage>
  );
};

export default Logout;
