import EventType from "../../enums/EventType";
import EventStatus from "../../enums/EventStatus";
import {
  AdminAuthorization,
  AuthorizedItems,
  Role,
} from "../../orbital-interfaces/authorized-items";
import { isAfter, parseISO } from "date-fns";
import authHelper, { SelectedPosition } from "../../util/authitems-helper";

export enum EventEditEnum {
  None,
  UndoCoverRequest,
  CreateShiftRequest,
  DeleteShiftRequest,
  CreateCoverRequest,
  Delete,
}

export interface Option {
  value: number;
  text: string;
  color: string;
}

export interface EventEditAuthorization {
  buttonText: string;
  enableConfirm: boolean;
  isReadonly: boolean;
  text: string;
  usage: EventEditEnum;
  eventTypeOptionList: Option[];
  eventStatusOptionList: Option[];
}

export const getEventNewAuthorization = (
  auth: AuthorizedItems
): EventEditAuthorization | null => {
  let location = auth.locations[0];
  let dept = auth.locations[0].departments[0];
  let position = auth.locations[0].departments[0].positions[0];
  let hasPositionFound = false;
  for (let i = 0; i < auth.locations.length && !hasPositionFound; i++) {
    for (
      let j = 0;
      j < auth.locations[i].departments.length && !hasPositionFound;
      j++
    ) {
      for (
        let k = 0;
        k < auth.locations[i].departments[j].positions.length &&
        !hasPositionFound;
        k++
      ) {
        if (auth.locations[i].departments[j].positions[k].hasPosition) {
          location = auth.locations[i];
          dept = auth.locations[i].departments[j];
          position = auth.locations[i].departments[j].positions[k];
          hasPositionFound = true;
        }
      }
    }
  }

  const selectedPosition = { location, position };

  let initialStatus = EventStatus.Request;
  if (auth.role === Role.BusinessAdmin || auth.role === Role.LocationAdmin) {
    initialStatus = EventStatus.Approved;
  } else if (
    auth.role === Role.Admin &&
    (dept.adminAuth & AdminAuthorization.CanEditSchedules) ===
      AdminAuthorization.CanEditSchedules
  ) {
    initialStatus = EventStatus.Approved;
  }

  const eventStatusOptions = getEventStatusOptionsList(
    auth,
    selectedPosition,
    auth.employeeId,
    initialStatus,
    false
  );
  const eventTypeOptions = getEventTypeOptionsList(
    auth,
    selectedPosition,
    auth.employeeId
  );

  return {
    buttonText: "",
    enableConfirm: true,
    isReadonly: false,
    text: "",
    usage: EventEditEnum.None,
    eventTypeOptionList: eventTypeOptions,
    eventStatusOptionList: eventStatusOptions,
  } as EventEditAuthorization;
};

// --------------------------------------------------------------------------------------
export const getEventEditAuthorization = (
  eventType: EventType,
  eventStatus: EventStatus,
  eventEmployeeId: number,
  eventStart: string,
  eventEnd: string,
  eventLocationId: number,
  eventDepartmentId: number,
  eventPositionId: number,
  eventConfirmed: boolean,
  auth: AuthorizedItems
): EventEditAuthorization | null => {
  const selectedPosition = authHelper.getSelectedPositionWithLocId(
    auth.locations,
    eventLocationId,
    eventPositionId
  );
  const eventStatusOptions = getEventStatusOptionsList(
    auth,
    selectedPosition,
    eventEmployeeId,
    eventStatus,
    true
  );
  const eventTypeOptions = getEventTypeOptionsList(
    auth,
    selectedPosition,
    eventEmployeeId
  );

  const retVal = {
    buttonText: "",
    enableConfirm: true,
    isReadonly: false,
    text: "",
    usage: EventEditEnum.None,
    eventTypeOptionList: eventTypeOptions,
    eventStatusOptionList: eventStatusOptions,
  } as EventEditAuthorization;

  if (auth.role === Role.BusinessAdmin) {
    return retVal;
  }

  if (auth.role === Role.LocationAdmin) {
    if (authHelper.isPositionSpecificEvent(eventType)) {
      if (auth.locations.filter((l) => l.id === eventLocationId).length === 0) {
        return null;
      }
    }

    return retVal;
  }

  const endOfEvent = parseISO(eventEnd);
  const now = new Date();
  const afterEnd = isAfter(now, endOfEvent);
  let employeeEvent = false;
  const adminAuth = authHelper.getAdminAuthorizationForDepartment(
    auth,
    eventDepartmentId
  );

  // If the user is in the Admin role
  if (auth.role === Role.Admin) {
    if (eventEmployeeId === auth.employeeId) {
      if (eventType === EventType.Clock) {
        if (
          (adminAuth & AdminAuthorization.CanEditOwnClockEvents) ===
          AdminAuthorization.CanEditOwnClockEvents
        ) {
          return retVal;
        } else {
          return null;
        }
      }

      if (
        (adminAuth & AdminAuthorization.CanEditSchedules) ===
        AdminAuthorization.CanEditSchedules
      ) {
        return retVal;
      }

      employeeEvent = true;
    } else {
      // Not the user's event, someone else's

      if (eventType === EventType.Clock) {
        if (
          (adminAuth & AdminAuthorization.CanEditEmployeeClockEvents) ===
          AdminAuthorization.CanEditEmployeeClockEvents
        ) {
          return retVal;
        } else {
          return null;
        }
      }

      if (
        authHelper.canEditSchedules(auth, eventLocationId, eventDepartmentId)
      ) {
        return retVal;
      } else {
        // If the admin has a position in the selected department
        if (authHelper.adminIsInDepartment(auth, eventDepartmentId)) {
          // Treat the admin as an employee for event editing
          employeeEvent = true;
        } else {
          // disable editing
          return null;
        }
      }
    }
  }

  if (auth.role === Role.Employee || employeeEvent) {
    if (eventEmployeeId === auth.employeeId) {
      if (afterEnd) {
        return null;
      }

      if (eventType === EventType.Clock) {
        return null;
      }

      if (eventType === EventType.Shift) {
        if (eventStatus === EventStatus.Approved) {
          return {
            ...retVal,
            buttonText: "Create Cover-Request",
            isReadonly: true,
            usage: EventEditEnum.CreateCoverRequest,
          };
        }

        if (eventStatus === EventStatus.Request) {
          return {
            ...retVal,
            buttonText: "Undo Shift-Request",
            enableConfirm: false,
            isReadonly: true,
            text: "Click OK to remove your Shift-Request",
            usage: EventEditEnum.DeleteShiftRequest,
          };
        }

        // else Shift Request-Denied
        return null;
      }

      if (eventType === EventType.Cover) {
        if (eventStatus === EventStatus.Request) {
          return {
            ...retVal,
            buttonText: "Undo Cover-Request",
            enableConfirm: true,
            isReadonly: true,
            usage: EventEditEnum.UndoCoverRequest,
          };
        }

        return null; // Cover Request-Denied
      }

      if (
        !authHelper.isPositionSpecificEvent(eventType) &&
        authHelper.canEditNonPositionEvents(auth, eventDepartmentId)
      ) {
        return {
          ...retVal,
          buttonText: "Delete Request",
          enableConfirm: false,
          isReadonly: true,
          text: "Click OK to delete this event",
          usage: EventEditEnum.Delete,
        };
      }

      return null;
    }

    // This is someone else's event being viewed by an Employee
    if (eventType === EventType.Cover && eventStatus === EventStatus.Request) {
      return {
        ...retVal,
        buttonText: "Make Shift-Request",
        enableConfirm: false,
        isReadonly: true,
        text: "",
        usage: EventEditEnum.CreateShiftRequest,
      };
    }

    return null;
  }

  return null;
};

// --------------------------------------------------------------------------------------
const getEventTypeOptionsList = (
  auth: AuthorizedItems,
  selectedPosition: SelectedPosition,
  eventEmployeeId: number
): Option[] => {
  if (
    !auth ||
    !auth.locations ||
    auth.locations.length === 0 ||
    !selectedPosition ||
    !selectedPosition.location ||
    !selectedPosition.location.settings
  ) {
    return [];
  }

  const eventTypeOptions: Option[] = [
    {
      value: EventType.Shift,
      text: "SHIFT",
      color: "green",
    },
  ];

  const locSettings = selectedPosition.location.settings;
  const dept = authHelper.getDepartmentFromPositionId(
    auth,
    selectedPosition.position.id
  );
  const adminAuth = authHelper.getAdminAuthorizationForDepartment(
    auth,
    dept!.id
  );

  if (auth.role === Role.BusinessAdmin || auth.role === Role.LocationAdmin) {
    eventTypeOptions.push({
      value: EventType.Clock,
      text: "CLOCK",
      color: "green",
    });
  } else if (auth.role === Role.Admin) {
    if (
      auth.employeeId === eventEmployeeId &&
      (adminAuth & AdminAuthorization.CanEditOwnClockEvents) ===
        AdminAuthorization.CanEditOwnClockEvents
    ) {
      eventTypeOptions.push({
        value: EventType.Clock,
        text: "CLOCK",
        color: "green",
      });
    } else if (
      auth.employeeId !== eventEmployeeId &&
      (adminAuth & AdminAuthorization.CanEditEmployeeClockEvents) ===
        AdminAuthorization.CanEditEmployeeClockEvents
    ) {
      eventTypeOptions.push({
        value: EventType.Clock,
        text: "CLOCK",
        color: "green",
      });
    }
  }

  if (auth.role === Role.BusinessAdmin || auth.role === Role.LocationAdmin) {
    eventTypeOptions.push({
      value: EventType.Cover,
      text: "COVER",
      color: "red",
    });
  } else if (
    auth.role === Role.Admin &&
    (adminAuth & AdminAuthorization.CanEditSchedules) ===
      AdminAuthorization.CanEditSchedules
  ) {
    eventTypeOptions.push({
      value: EventType.Cover,
      text: "COVER",
      color: "red",
    });
  }

  if (locSettings.ShowUnavailableEventType) {
    eventTypeOptions.push({
      value: EventType.Unavailable,
      text: "UNAVAILABLE",
      color: "yellow",
    });
  }

  if (locSettings.ShowVacationEventType) {
    eventTypeOptions.push({
      value: EventType.Vacation,
      text: "VACATION",
      color: "yellow",
    });
  }

  if (locSettings.ShowAbsentEventType) {
    const peTypes = auth.payrollEvents
      .filter(
        (pe) =>
          pe.positionIds.includes(selectedPosition.position.id) &&
          !pe.name.startsWith("--")
      )
      .map((pe) => {
        return { value: pe.id, text: pe.name, color: "yellow" };
      });
    return eventTypeOptions.concat(peTypes);
  }

  return eventTypeOptions;
};

// --------------------------------------------------------------------------------------
const getEventStatusOptionsList = (
  auth: AuthorizedItems,
  selectedPosition: SelectedPosition,
  eventEmployeeId: number,
  eventStatusId: EventStatus,
  isEditingExistingEvent: boolean
): Option[] => {
  const eventStatusOptions: Option[] = [];
  const dept = authHelper.getDepartmentFromPositionId(
    auth,
    selectedPosition.position.id
  );
  const adminAuth = authHelper.getAdminAuthorizationForDepartment(
    auth,
    dept!.id
  );

  if (auth.role === Role.LocationAdmin || auth.role === Role.BusinessAdmin) {
    eventStatusOptions.push({
      value: EventStatus.Approved,
      text: "APPROVED",
      color: "black",
    });
  } else if (
    auth.role === Role.Admin &&
    (adminAuth & AdminAuthorization.CanEditSchedules) ===
      AdminAuthorization.CanEditSchedules
  ) {
    eventStatusOptions.push({
      value: EventStatus.Approved,
      text: "APPROVED",
      color: "black",
    });
  }

  eventStatusOptions.push({
    value: EventStatus.Request,
    text: "REQUEST",
    color: "red",
  });

  if (auth.role === Role.LocationAdmin || auth.role === Role.BusinessAdmin) {
    eventStatusOptions.push({
      value: EventStatus.RequestDenied,
      text: "REQUEST-DENIED",
      color: "black",
    });
  } else if (
    auth.role === Role.Admin &&
    (adminAuth & AdminAuthorization.CanEditSchedules) ===
      AdminAuthorization.CanEditSchedules
  ) {
    eventStatusOptions.push({
      value: EventStatus.RequestDenied,
      text: "REQUEST-DENIED",
      color: "black",
    });
  }

  if (isEditingExistingEvent) {
    if (
      eventStatusOptions.filter((es) => es.value === eventStatusId).length === 0
    ) {
      switch (eventStatusId) {
        case EventStatus.Approved:
          eventStatusOptions.push({
            value: EventStatus.Approved,
            text: "APPROVED",
            color: "black",
          });
          break;
        case EventStatus.Request:
          eventStatusOptions.push({
            value: EventStatus.Request,
            text: "REQUEST",
            color: "red",
          });
          break;
        case EventStatus.RequestDenied:
          eventStatusOptions.push({
            value: EventStatus.RequestDenied,
            text: "REQUEST-DENIED",
            color: "black",
          });
          break;
      }
    }
  }

  return eventStatusOptions;
};
