import React, { useState, useEffect, useContext } from "react";
// import "./ClockPanel.scss";
import "../../global-styles.scss";
import { ApplicationUserContext } from "../../context/ApplicationUserContext";
import useDialogBox from "../../hooks/useDialogBox";
import {
  IonContent,
  IonItem,
  IonLabel,
  IonList,
  IonListHeader,
  IonModal,
  IonSpinner,
  useIonViewWillEnter,
  useIonViewWillLeave,
} from "@ionic/react";
import { getDateString } from "../../util/time-helper";
import TodayDailyNoteCard from "../TodayDailyNoteCard";
import TodayEventCard from "../TodayEventCard";
import { EventInformation } from "../../orbital-interfaces/TEC";
import EditEventModal from "../EditEvent/EditEventModal";
import { RefresherEventDetail } from "@ionic/core";
import UserAPI from "../../api/UserAPI";
import useIonicRefresher from "../../hooks/useIonicRefresher";
import { initialHomePageData } from "../../orbital-interfaces/authorized-items";
import TodayEventCardSkeleton from "../TodayEventCardSkeleton";
import { EventEditAuthorization } from "../EditEvent/EditEventAuthorization";
import {
  getEditorValuesFromEventInformation,
  INITIAL_EDITOR_VAUES,
} from "../EditEvent/EditEventModalState";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import { useInterval } from "../../hooks/useInterval";
import { getConstants } from "../../api/environment";

interface ClockTodayTabProps {
  refreshToggle: boolean;
  onUpdate: () => void;
}

const ClockTodayTab: React.FC<ClockTodayTabProps> = ({
  refreshToggle,
  onUpdate,
}: ClockTodayTabProps) => {
  const doRefresh = (e: CustomEvent<RefresherEventDetail>) => {
    getData(e);
  };

  const [homePageData, setHomePageData] = useState(initialHomePageData);
  const refresher = useIonicRefresher(doRefresh);
  const { user } = React.useContext(ApplicationUserContext);
  const dlg = useDialogBox();
  const [showEventModal, setShowEventModal] = useState(false);
  const [editEventId, setEditEventId] = useState(null as number | null);
  const [eventEditAuth, setEventEditAuth] = useState(
    null as EventEditAuthorization | null
  );
  const [initialEditEvent, setInitialEditEvent] = useState(
    INITIAL_EDITOR_VAUES
  );
  const [isLoading, setIsLoading] = useState(false);
  const [isShowing, setIsShowing] = useState(false);
  const { auth } = useContext(AuthorizedItemsContext);

  // ------------------------------------------------------------------------------------
  useIonViewWillEnter(() => {
    setIsShowing(true);
  });

  // ------------------------------------------------------------------------------------
  useIonViewWillLeave(() => {
    setIsShowing(false);
  });

  // ------------------------------------------------------------------------------------
  useInterval(
    () => {
      getData();
    },
    isShowing && !showEventModal,
    getConstants(user).refreshScheduleTimer
  );

  // ------------------------------------------------------------------------------------
  const onEventClick = (
    ev: EventInformation,
    newEventEditAuth: EventEditAuthorization | null
  ) => {
    setEditEventId(ev.ScheduleEventId);
    setEventEditAuth(newEventEditAuth);
    setInitialEditEvent(getEditorValuesFromEventInformation(auth, ev));
    setShowEventModal(true);
  };

  // ------------------------------------------------------------------------------------
  function getData(e?: CustomEvent<RefresherEventDetail>) {
    try {
      UserAPI.getHomePageData(user).then((response) => {
        console.log("ClockTodayTab.getData: got homepage data");
        setHomePageData(response);
        setIsLoading(false);
        if (e) {
          e.detail.complete();
          onUpdate();
        }
      });
    } catch (e) {
      console.log("Error in ClockTodayTab.getData: " + e.message);
    }
  }

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    // eslint-disable-next-line
    if (user && user.isLoggedin) {
      setIsLoading(true);
      getData(undefined);
    }

    // eslint-disable-next-line
  }, [refreshToggle]);

  // ------------------------------------------------------------------------------------
  const onEventUpdated = () => {
    getData(undefined);
    setShowEventModal(false);
  };

  return (
    <>
      <IonContent className="outer-content">
        {refresher.ionicRefresherJsx}

        <IonList className="todaylist" lines="none">
          <IonListHeader className="list-header" mode="ios">
            <IonLabel className="label-list-header" mode="ios">
              {getDateString(new Date())}
            </IonLabel>
          </IonListHeader>
          {isLoading &&
            ((homePageData.dailyNotes && homePageData.dailyNotes.length > 0) ||
              (homePageData.currentEvents &&
                homePageData.currentEvents.length > 0)) && (
              <IonSpinner className="center" name="dots" />
            )}

          {isLoading &&
            (!homePageData.dailyNotes ||
              homePageData.dailyNotes.length === 0 ||
              !homePageData.currentEvents ||
              homePageData.currentEvents.length === 0) && (
              <>
                <TodayEventCardSkeleton />
                <TodayEventCardSkeleton />
              </>
            )}

          {homePageData.dailyNotes &&
            homePageData.dailyNotes.map((note) => (
              <IonItem key={note.DailyNoteId}>
                <TodayDailyNoteCard
                  authItems={homePageData.auth}
                  oneLine={false}
                  dailyNote={note}
                />
              </IonItem>
            ))}
          {homePageData.currentEvents.map((ev) => (
            <IonItem key={ev.ScheduleEventId}>
              <TodayEventCard
                onEdit={onEventClick}
                locations={homePageData.auth.locations}
                event={ev}
              />
            </IonItem>
          ))}
        </IonList>
      </IonContent>

      <IonModal
        isOpen={showEventModal}
        onDidDismiss={() => setShowEventModal(false)}
      >
        <EditEventModal
          isOpen={showEventModal}
          auth={homePageData.auth}
          onCancelModal={() => setShowEventModal(false)}
          onDismissModal={() => onEventUpdated()}
          scheduleEventId={editEventId}
          eventEditAuth={eventEditAuth!}
          initialEvent={initialEditEvent}
        />
      </IonModal>

      {dlg.alertBoxJsx}
    </>
  );
};

export default ClockTodayTab;
