import React from "react";
import { DailyNotes } from "../orbital-interfaces/DailyNotes";
// import "./TodayDailyNoteCard.scss";
import "../global-styles.scss";
import { AuthorizedItems } from "../orbital-interfaces/authorized-items";

interface TodayNoteCardProps {
  oneLine: boolean;
  dailyNote?: DailyNotes;
  authItems: AuthorizedItems;
}

// ---------------------------------------------------------------------------------
const getDeptName = (note: DailyNotes, authItems: AuthorizedItems) => {
  let dept = "";
  if (authItems.locations.length >= 1) {
    dept =
      authItems.locations.filter((l) => l.id === note.LocationId)[0].abbr + "/";
  }

  return note.DepartmentId === -1
    ? dept + "All Departments"
    : dept + note.DepartmentName;
};

// ==============================================================================
const TodayNoteCard: React.FC<TodayNoteCardProps> = ({
  authItems,
  oneLine,
  dailyNote,
}: TodayNoteCardProps) => {
  if (!dailyNote) {
    return <div />;
  }

  return (
    <button className="card daily-note">
      <div className="event-color-col" />
      <div className="card-second-col">
        <div className="dept-position-row">{getDeptName(dailyNote, authItems)}</div>
        <div className={oneLine ? "note-text-row-oneline" : "note-text-row"}>
          {dailyNote.Notes}
        </div>
      </div>
    </button>
  );
};

export default TodayNoteCard;
